/**
 * Bio component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react"
import { StaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"

import { rhythm } from "../utils/typography"

function Bio() {
  return (
    <StaticQuery
      query={bioQuery}
      render={data => {
        const { author, social } = data.site.siteMetadata
        return (
          <div
            style={{
              display: `flex`,
              marginBottom: rhythm(2.5),
            }}
          >
            <Image
              fixed={data.avatar.childImageSharp.fixed}
              alt={author}
              style={{
                marginRight: rhythm(1 / 2),
                marginBottom: 0,
                minWidth: 50,
                borderRadius: `100%`,
              }}
              imgStyle={{
                borderRadius: `50%`,
              }}
            />
            <p>
              Hi! I'm <strong>{author}</strong>. I am a recovering engineering manager turned
              software developer who wants to share some of my experiences leading and managing
              people to help out people who are looking to grow in these areas. If you're 
              interested in software engineering concepts, check out my technical writing at{` `} 
              <a href="https://testdrivenalgorithms.com">testdrivenalgorithms.com</a>.<br></br>
               If you like what you read connect with me on social!<br></br>
              {` `}
              Social:&#160;
              <a href={`https://twitter.com/${social.twitter}`}>
                Twitter
              </a>
              &#160;
              <a href={`https://linkedin.com/in/${social.linkedin}`}>
                LinkedIn
              </a>
              &#160;
              <a href={`https://github.com/${social.github}`}>
                GitHub
              </a>
            </p>
          </div>
        )
      }}
    />
  )
}

const bioQuery = graphql`
  query BioQuery {
    avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
      childImageSharp {
        fixed(width: 50, height: 50) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        author
        social {
          twitter
          linkedin
          github
        }
      }
    }
  }
`

export default Bio
